<template>
  <div class="org">
    <div class="data1">
      <div class="selects">
        <div class="select-titles">
          <el-row :gutter="24">
            <el-col :span="6"><div class="common-item">用户名</div></el-col>
            <el-col :span="6"><div class="common-item"></div></el-col>
          </el-row>
        </div>
        <div class="select-content">
          <el-row :gutter="24">
            <el-col :span="6">
              <div class="common-item selsct-common">
                <el-input v-model="queryFilter.mid" placeholder="请输入用户名"></el-input>
              </div>
            </el-col>
            <el-col :span="6">
              <el-button class="blue" type="primary" @click="initData">查询</el-button>
            </el-col>
          </el-row>
        </div>
      </div>
      <div class="table-data">
        <iot-table
          :tableHeader="tableHeader"
          :tableData="tableData"
          :pagination="pagination"
          :loading="loading"
          @reloaddata="initData"
        >
          <div slot="col-province" slot-scope="scope"><p>{{scope.row.province || ''}}{{scope.row.city || ''}}{{scope.row.county || ''}}{{scope.row.village || ''}}</p></div>
          <div slot="col-confirm" slot-scope="scope">
            <el-button @click="handleDelete(scope.row.mid)" type="text" size="small"><span class="iconfont iconshanchu"></span></el-button>
          </div>
        </iot-table>
      </div>
    </div>
  </div>
</template>

<script>
import { findUsers, delUsernew } from '@/api/system'
import iotTable from '@/components/iotTable'
import msgUtil from '@/utils/msgUtil'

export default {
  name: 'Permission',
  components: {
    iotTable
  },
  data() {
    return {
      loading: false,
      queryFilter: {
        mid: ''
      },
      type: [
        {
          code: '',
          name: '全部'
        },
        {
          code: 'HQ',
          name: '总代'
        },
        {
          code: 'CH',
          name: '代理商'
        }
      ],
      pagination: {
        page: 1,
        per_page: 10,
        total: 0
      },
      tableData: [],
      tableHeader: [
        { prop: 'mid', label: '用户名' },
        { prop: 'province', label: '代理地区', type: 'slot' },
        { prop: 'company', label: '所属单位' },
        { prop: 'role_name', label: '角色' },
        { prop: 'confirm', label: '操作', type: 'slot' }
      ]
    }
  },
  mounted() {
    this.initData()
  },
  methods: {
    async initData() {
      this.loading = true
      const params = {
        mid: this.queryFilter.mid ? this.queryFilter.mid : undefined,
        status: 0,
        page: this.pagination.page,
        per_page: this.pagination.per_page
      }
      try {
        const { data } = await findUsers(params)
        this.tableData = data.items
        this.pagination = {
          total: data.page_info.total,
          page: data.page_info.page,
          per_page: data.page_info.per_page
        }
        this.loading = false
      } catch (error) {
        this.loading = false
        msgUtil.error('查询数据失败', msgUtil.findErrorMsg(error))
      }
    },
    handleDelete(mid) {
      msgUtil.confirmuser('确定删除用户？', mid, '删除后用户将无法登陆系统', (verifycode) => {
        delUsernew(mid, verifycode).then(resp => {
          if (resp.status === 200) {
            msgUtil.info(`删除用户${mid}成功`)
          } else {
            msgUtil.error('删除失败', resp.msg)
          }
        }, error => {
          msgUtil.error('删除失败', msgUtil.findErrorMsg(error))
        })
      })
    }
  }
}
</script>

